@import url(https://fonts.googleapis.com/css?family=Muli:400,500,600,700,800,900&display=swap);
body {
  margin: 0;
  font-family: Muli;
  color: #707275;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #dde1e7;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.neumorphism {
  border-radius: 20px;
  background: #dde1e7;
  box-shadow: 20px 20px 60px #c3d1d9, -20px -20px 60px #ffffff;
}

.neumorphism-inside {
  border-radius: 20px;
  background: #dde1e7;
  box-shadow: inset 20px 20px 60px #bcbfc4, inset -20px -20px 60px #feffff;
}

.neumorphism-icon {
  border-radius: 20px;
  background: #dde1e7;
  box-shadow: 10px 10px 30px #bcbfc4, -20px -20px 60px #feffff;
}

.btn-grad {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#00d2ff), to(#1e90ff));
  background-image: linear-gradient(to bottom, #00d2ff 0%, #1e90ff 100%);
}
.btn-grad:hover {
  background-position: right center;
}

.btn-grad-orange {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#feb47b), to(#ff7e5f));
  background-image: linear-gradient(to bottom, #feb47b 0%, #ff7e5f 100%);
}
.btn-grad-orange:hover {
  background-position: right center;
}

.btn-grad-light {
  background-image: -webkit-gradient(linear, left top, left bottom, from(#85d880), to(#3fbf9e));
  background-image: linear-gradient(to bottom, #85d880 0%, #3fbf9e 100%);
}
.btn-grad-light:hover {
  background-position: right center;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

